import React, { Component } from 'react';
import MyBio from './MyBio';
import AboutImage from './AboutImage';

class AboutPage extends Component {
    render(){
        return(
            <div style={{paddingBottom:'5%'}}> 
                <MyBio/>
                <AboutImage/>
                <table class="table table-borderless" >
                <tbody>
                    <tr>
                        <td style={{color:'white'}}>
                            Hamish Misselbrook- Vocals/Ac.Guitar/Keys
                        </td>
                    </tr>
                    <tr>
                        <td style={{color:'white'}}>
                            Cameron Watt- Vocals/E.Guitar
                        </td>
                    </tr>
                    <tr>
                        <td style={{color:'white'}}>
                            Connor Lilliott- Vocals/Bass/Keys/Cello
                        </td>
                    </tr>
                    <tr>
                        <td style={{color:'white'}}>
                            Reece Carr- Lead Cowbell/E.Guitar/Bass
                        </td>
                    </tr>
                    <tr>
                        <td style={{color:'white'}}>
                            Murray Steele- Rhythm Cowbell/Drums
                        </td>
                    </tr>
                </tbody>
                </table>  
            </div>
            
        )
    }
}

export default AboutPage;