import React from 'react';
import logo from './AP1_4595.jpg';

function ContactImage() {

  return (
    <div style={{position: 'relative', textAlign: 'center', color: 'white'}}>
    <img src={logo} style={{width: '100%', paddingBottom:20, opacity:0.4}} alt='Ninth Degree Cover' />
    <div style={{position: 'absolute', top: '50%', left: '50%',  transform: 'translate(-50%, -50%)', fontSize: '1.4em'}}>CONTACT US</div>
    </div>
  );
}

export default ContactImage;