import React from 'react';
import './App.css';
import HomepageImage from './components/HomePageImage';
import MyNavbar from './components/Navbar';
import MyLinks from './components/MyLinks';
import AboutPage from './components/AboutPage';
import ContactPage from './components/ContactPage';
import 'bootstrap/dist/css/bootstrap.min.css';
import ContactImage from './components/ContactImage';
import Footer from './components/Footer';
import { Navbar, Nav, Button } from 'react-bootstrap';
import logo from './components/TransparentLogo.png';
import EventImage from './components/EventImage';
import EventsEmbed from './components/EventsEmbed';

class App extends React.Component {

linkToShop() {
    window.location.href='https://ninthdegreeuk.bigcartel.com'
}

scrollToAbout(){
    window.location.href = "#about"
}

scrollToContact(){
  window.location.href = "#contact"
}

scrollToTop(){
    window.scrollTo(0, 0);
}

  render(){
    return (
      <div className="App" style={{float:'left'}}>
        <header className="App-header">
          <div>
          <Navbar collapseOnSelect fixed="top" expand="lg" bg="dark" variant="dark" style={{padding: '2vh', backgroundColor:'#434d56' }}>
            <Navbar.Brand href="#home"  onClick={this.scrollToTop} ><img src= {logo} style={{height: '10vh' }} ></img></Navbar.Brand>
            <Navbar.Toggle aria-controls="responsive-navbar-nav" />
            <Navbar.Collapse id="responsive-navbar-nav">
            <Nav className="mr-auto">
                <Nav.Link> <Button variant="dark" onClick={this.scrollToAbout} style={{fontSize: 'calc(10px + 2vmin)'}}>About</Button></Nav.Link>
                <Nav.Link> <Button variant="dark" onClick={this.linkToShop} style={{fontSize: 'calc(10px + 2vmin)'}}>Shop</Button></Nav.Link>
                <Nav.Link> <Button variant="dark" onClick={this.scrollToContact} style={{fontSize: 'calc(10px + 2vmin)'}}>Contact</Button></Nav.Link>
            </Nav>
            </Navbar.Collapse>
          </Navbar>
          </div>
          <div style={{paddingTop:'14.7vh'}}>
          <MyLinks/>
          <EventImage/>
          <div id='about'>
          </div>
          <HomepageImage/>
         
          <AboutPage/>
         
          <div id='contact'>
          <ContactImage/>
          <ContactPage/>
          </div>
          </div>
          <footer style={{backgroundColor:'#434d56', width:'100%'}}>
          <Footer/>
          </footer>
          </header>
      </div>
    );
  }
}

export default App;
