import React from 'react';
import { Navbar, Nav, Button } from 'react-bootstrap';
import logo from './ndlogo.png';


class MyNavbar extends React.Component{

    linkToShop() {
        window.location.href='https://ninthdegreeuk.bigcartel.com'
    }

    scrollToAbout(){
        window.scrollTo(0, 1300);
    }

    scrollToContact(){
        window.scrollTo(0, 2000);
    }

    scrollToTop(){
        window.scrollTo(0, 0);
    }

    render(){
        return (
            <Navbar collapseOnSelect fixed="top" expand="lg" bg="dark" variant="dark" style={{padding: '2vh', backgroundColor:'#434d56' }}>
            <Navbar.Brand href="#home"  onClick={this.scrollToTop} ><img src= {logo} style={{height: '10vh' }} ></img></Navbar.Brand>
            <Navbar.Toggle aria-controls="responsive-navbar-nav" />
            <Navbar.Collapse id="responsive-navbar-nav">
            <Nav className="mr-auto">
                <Nav.Link> <Button variant="dark" onClick={this.scrollToAbout} style={{fontSize: 'calc(10px + 2vmin)'}}>About</Button></Nav.Link>
                <Nav.Link> <Button variant="dark" onClick={this.linkToShop} style={{fontSize: 'calc(10px + 2vmin)'}}>Shop</Button></Nav.Link>
                <Nav.Link> <Button variant="dark" onClick={this.scrollToContact} style={{fontSize: 'calc(10px + 2vmin)'}}>Contact</Button></Nav.Link>
            </Nav>
            </Navbar.Collapse>
            </Navbar>
        );
    }
}


export default MyNavbar;